import { Formik,/*  Form, */ Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import s from '../Login/Login.module.css';
//import { Navigate } from "react-router-dom";
import { NavLink } from 'react-router-dom';
import { useState } from 'react';
import passEye from '../../images/passEye.svg'
import { Input, Form, FormItem } from 'formik-antd'

/* matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{2,})/,
"   5  255 ,   ,  ,      "
) */

const RegistrationForm = (props) => {
	const [passFlag, setPassFlag] = useState(true);

	return (
		<Formik
			initialValues={{ email: '', password: '', repeatPassword: '', name: '', secondName: '', middleName: ''}}
			validationSchema={Yup.object({
				email: Yup.string().email(' email').required('').max(255, 'Email     255 '),
				password: Yup.string().min(5, '   5  255 ').max(255, "   5  255 ").required(''),
				repeatPassword: Yup.string().when("password", {
					is: val => (val && val.length > 0 ? true : false),
					then: Yup.string().oneOf(
					  [Yup.ref("password")],
					  "  "
					)
				  }).required(''),
				name: Yup.string().max(32, '     32 ').required(''),
				secondName: Yup.string().max(32, '     32 ').required(''),
				middleName: Yup.string().max(32, '     32 '),
			  })}
			onSubmit={(values, { setSubmitting, setStatus }) => {
				console.log(JSON.stringify(values, null, 2));
				values.password = '';
				setSubmitting(false);
			}}
		>
			{({ isSubmitting, status }) => (
				<Form className={s.form}>
					<div className={`${s.formWrapper} ${status && s.error}`}> 
						<div className={s.form_navigate}>
							<NavLink className={s.form_navigateEl} to={'/login'}></NavLink>
							<div><NavLink className={s.form_navigateEl} to={'/register'}></NavLink></div>
						</div>
						<div className={s.line_navigate}></div>
						<div className={s.errorText}>{status}</div> 
						<FormItem name="name" >
               				<Input name="name" placeholder="*" className={s.elemForm}/>
              			</FormItem>
						<FormItem name="secondName" >
               				<Input name="secondName" placeholder="*" className={s.elemForm}/>
              			</FormItem>
						{/* <div >
							<Field className={s.elemForm} name="middleName" placeholder=""/>
							<ErrorMessage className={s.errorMes} name="middleName" component="div" />
						</div> */}
						<FormItem name="middleName" >
               				<Input name="middleName" placeholder="" className={s.elemForm}/>
              			</FormItem>
						{/* <div >
							<Field className={s.elemForm} type="email" name="email" placeholder="Email*"/>
							<ErrorMessage className={s.errorMes} name="email" component="div" />
						</div> */}
						<FormItem name="email" >
               				<Input name="email" placeholder="Email*" className={s.elemForm}/>
              			</FormItem>
						<FormItem name="password" >
               				<Input.Password name="password" placeholder="Password" className={s.elemForm}/>
              			</FormItem>
						<div>
							<Field className={s.elemForm} type="password" name="repeatPassword" placeholder="Repeat rassword*"/>
							<ErrorMessage className={s.errorMes} name="repeatPassword" component="div" />
						</div>
						<button className={s.bLogin} type="submit" disabled={isSubmitting}></button>
					</div>
				</Form>
			)}
		</Formik>
  );
}

export const Registration = (props) => {

	return ( 
		<div>
			<RegistrationForm {...props}/>
		</div>
	 );
}
